import { Grid, Typography, withStyles } from '@material-ui/core';
import React from 'react';
import { Link } from 'react-router-dom';
import style from "./style"
import HomeIcon from "@material-ui/icons/Home";
import ReplayIcon from "@material-ui/icons/Replay";
import { connect } from 'react-redux';
import { compose } from 'redux';
function ControlResult(props) {
    const { classes, order, results,courseid } = props;
    function onChangeOrder(number){
props.onChangeOrder(number)
    }
    function renderGrade (){
        var grade = 0;
        results.forEach((item) => {
          if (item.youranswer === item.answer) {
            grade += 1;
          }
        });
        return (
          <div>
            <Grid>
              <Typography>GRADE</Typography>
            </Grid>
            <Grid>
              {grade}/{results.length}
            </Grid>
          </div>
        );
      };
   function renderItem () {
        
        if (results.length > 0) {
          return results.map((item, index) => {
            return (
              <Grid
                key={index}
                item
                md={2}
                className={classes.rootItem}
                style={{
                  color: item.numberorder === order ? "blue" : "",
                }}
              >
                <div
                  className={classes.item}
                  style={{
                    backgroundColor:
                      item.youranswer === item.answer ? "#01DF01" : "#FF0040",
                  }}
                  onClick={() => {
                    onChangeOrder(item.numberorder);
                  }}
                >
                  {item.numberorder}
                </div>
              </Grid>
            );
          });
        }
      };
    return (
        <div style={{ paddingRight: 30 }}>
        <Grid container className={classes.header}>
          <Grid md={3} className={classes.rootlink}>
            <Link to="/" className={classes.link}>
              <HomeIcon />
              <Typography>Home</Typography>
            </Link>
          </Grid>
          <Grid md={6} className={classes.score}>
            {renderGrade()}
          </Grid>
          <Grid md={3} className={classes.rootlink}>
            <Link
              className={classes.link}
              to={`/test/${courseid}`}
            >
              <ReplayIcon />
              <Typography>Làm lại</Typography>
            </Link>
          </Grid>
        </Grid>
        <Grid container style={{ border: "1px solid black", borderTop: 0 }}>
          {renderItem()}
        </Grid>
      </div>
    );
}

const mapStateToProps = (state) => {};
const withConnect = connect(mapStateToProps, null);
export default compose(withStyles(style),withConnect)(ControlResult);