import { Button, CardMedia, FormControl, FormControlLabel, Grid, Radio, RadioGroup, Typography, withStyles } from '@material-ui/core';
import React from 'react';
import { Redirect } from 'react-router-dom';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import { compose } from 'redux';
import style from "./style"
function ResultItem(props) {
    const { results, order, classes } =props;
    const length = results.length;
    function onChangeOrder(number){
        props.onChangeOrder(number);
    }
    function onChange(){

    }
    function renderOption() {

    };
   function renderImage  (question){
        if (question && question.image) {
          return (
            <CardMedia
              image={ `/${question.image}`}
              title="image"
              style={{ marginTop: 6, height: 300 }}
            ></CardMedia>
          );
        }
      };
   
  function  renderAlert (option, answer, youranswer) {
        if (option === youranswer && youranswer === answer) {
          return (
            <CheckIcon style={{marginTop:7,color:"green"}}/>
          );
        } else if (option === youranswer && youranswer !== answer) {
          return (
           <CloseIcon  style={{marginTop:7,color:"red"}}/>
          );
        } else if (option === answer) {
          return (
            <CheckIcon style={{marginTop:7,color:"green"}}/>
          );
        }
      };
  function  renderResult () {
      
        let xhtml = null;
        if (results.length > 0) {
          xhtml = results.map((item, index) => {
            return (
              <Grid
                key={index}
                style={{ display: item.numberorder === order ? "" : "none" }}
              >
                <Typography style={{ marginBottom: 30 }}>
                  <b>{item ? item.title : ""}</b>
                </Typography>
                {renderImage(item)}
             
                <Typography style={{ marginBottom: 15, marginTop: 15 }}>
                  <b>Question {item.numberorder} :</b>
                </Typography>
                <FormControl component="div" style={{ width: "100%" }}>
                  <RadioGroup
                    name="gender1"
                    value={item.youranswer ? item.youranswer : 0}
                    onChange={onChange}
                    style={{ marginLeft: 20 }}
                  >
                    <div style={{ display: "inherit" }}>
                      <FormControlLabel
                        value={1}
                        control={<Radio />}
                        label={`A: ${item.option1}`}
                      />
                      {renderAlert(1, item.answer, item.youranswer)}
                    </div>
                    <div style={{ display: "inherit" }}>
                      <FormControlLabel
                        value={2}
                        control={<Radio />}
                        label={`A: ${item.option2}`}
                      />
                      {renderAlert(2, item.answer, item.youranswer)}
                    </div>
                    <div style={{ display: "inherit" }}>
                      <FormControlLabel
                        value={3}
                        control={<Radio />}
                        label={`A: ${item.option3}`}
                      />
                      {renderAlert(3, item.answer, item.youranswer)}
                    </div>
                    <div style={{ display: "inherit" }}>
                      <FormControlLabel
                        value={4}
                        control={<Radio />}
                        label={`A: ${item.option4}`}
                      />
                      {renderAlert(4, item.answer, item.youranswer)}
                    </div>
                  </RadioGroup>
                </FormControl>
              </Grid>
            );
          });
        } else {
          xhtml = <div>Bạn chưa có bài kiểm tra nào</div>;
        }
        return xhtml;
      };
    return (
        <div className={classes.border}>
        {renderResult()}
        <Grid className={classes.rootBtn}>
          {order !== 1 ? (
            <Button
              variant="outlined"
              className={classes.btn}
              onClick={() => {
                onChangeOrder(-1);
              }}
            >
              Back
            </Button>
          ) : (
            ""
          )}
          {order !== length ? (
            <Button
              className={classes.btn}
              variant="outlined"
              onClick={() => {
                onChangeOrder(1);
              }}
            >
              Next
            </Button>
          ) : (
            ""
          )}
        </Grid>
      </div>
    );
}

export default compose(withStyles(style))(ResultItem);