import * as types from "../constant/Course";
const initial = [];
const courseReducer = (state = initial, actions) => {
  switch (actions.type) {
    case types.FETCH_COURSE:
      return [...state];
    case types.FETCH_COURSE_SUCCESS:
      const { courses } = actions.payload;
      console.log("Data", courses);
      return courses;
    case types.FETCH_COURSE_FAILED:
      return [...state];
    default:
      return [...state];
  }
};
export default courseReducer;
