import * as types from "../../constant/LoginActions";
export const login = (user) => ({
  type: types.LOGIN,
  payload: {
    user,
  },
});
export const loginSuccess = (jwt) => ({
  type: types.LOGIN_SUCCESS,
  payload: {
    jwt,
  },
});
export const loginFailed = (error) => ({
  type: types.LOGIN_FAILED,
  payload: {
    error,
  },
});
export const fetchUser = () => ({
  type: types.FETCH_USER,
});
export const fetchUserSuccess = (user) => ({
  type: types.FETCH_USER_SUCCESS,
  payload: {
    user,
  },
});
export const fetchUserFailed = (error) => ({
  type: types.FETCH_USER_FAILED,
  payload: {
    error,
  },
});
export const logout = () => ({
  type: types.LOGOUT,
});
export const logoutUser = () => ({
  type: types.LOGOUT_USER,
});
export const updateUserImage = (image) => ({
  type: types.UPDATE_IMAGE_USER,
  payload: { image },
});
export const updateUserImageSuccess = (user) => ({
  type: types.UPDATE_IMAGE_USER_SUCCESS,
  payload: { user },
});
export const updateUserImageFailed = (error) => ({
  type: types.UPDATE_IMAGE_USER_FAILED,
  payload: {
    error,
  },
});
export const updateUser = (user) => ({
  type: types.UPDATE_USER,
  payload: {
    user,
  },
});
export const updateUserSuccess = (user) => ({
  type: types.UPDATE_USER_SUCCESS,
  payload: {
    user,
  },
});
export const updateUserFailed = (error) => ({
  type: types.UPDATE_USER_FAILED,
  payload: {
    error,
  },
});
