import {
  AppBar,
  Badge,
  IconButton,
  InputBase,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
  withStyles,
} from "@material-ui/core";
import { toggleDrawer } from "../../actions/DrawersActions";
import React, { Component } from "react";
import { compose } from "redux";
import style from "./style";
import SearchIcon from "@material-ui/icons/Search";
import MenuIcon from "@material-ui/icons/Menu";
import { AccountCircle } from "@material-ui/icons";
import { connect } from "react-redux";
import MenuLink from "../FormHelper/RenderLink/renderLink";
import { logout, logoutUser } from "../../actions/LoginActions";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import { Redirect } from "react-router";
class Header extends Component {
  state = {
    anchorEl: "",
    redirectProfile: false,
  };
  onLogout = () => {
    this.props.onLogout();
    this.props.onlogoutUser();
  };
  toProfile = () => {
    this.setState({
      redirectProfile: true,
    });
  };
  handleProfileMenuOpen = (event) => {
    this.setState({
      anchorEl: event.currentTarget,
    });
  };
  isMenuOpen = this.state.anchorEl ? true : false;
  handleMenuClose = () => {
    this.setState({
      anchorEl: null,
    });
  };
  renderMenu = () => {
    const { login, classes } = this.props;
    return (
      <Menu
        id="123"
        anchorEl={this.state.anchorEl}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
        keepMounted
        transformOrigin={{ vertical: "top", horizontal: "right" }}
        open={this.state.anchorEl ? true : false}
        onClose={this.handleMenuClose}
      >
        {login ? (
          <div>
            <MenuItem onClick={this.toProfile}>Profile</MenuItem>
            <MenuItem onClick={this.onLogout}>Logout</MenuItem>
          </div>
        ) : (
          <div>
            <MenuItem onClick={this.handleMenuClose}>
              <MenuLink
                to="/login"
                label="Login"
                activeOnlyWhenExact={true}
                className={classes.link}
              />
            </MenuItem>
          </div>
        )}
      </Menu>
    );
  };
  onToggle = () => {
    this.props.onToggleDrawer();
  };
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.grow}>
        {this.state.redirectProfile ? <Redirect to="/my-profile" /> : ""}
        <AppBar position="static">
          <Toolbar>
            <IconButton
              edge="start"
              className={classes.menuButton}
              color="inherit"
              aria-label="open drawer"
              onClick={this.onToggle}
            >
              <MenuIcon />
            </IconButton>
            <Typography className={classes.title} variant="h6" noWrap>
              Learn Code
            </Typography>
            {/* <div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon />
              </div>
              <InputBase
                placeholder="Search…"
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
                inputProps={{ "aria-label": "search" }}
              />
            </div> */}
            <div className={classes.grow} />
            <div className={classes.sectionDesktop}>
              {/* <IconButton aria-label="cart" style={{ marginRight: 20 }}>
                <Badge badgeContent={4} max={9} color="secondary">
                  <MailOutlineIcon />
                </Badge>
              </IconButton> */}
              <IconButton
                edge="end"
                aria-label="account of current user"
                aria-haspopup="true"
                aria-controls="123"
                onClick={this.handleProfileMenuOpen}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        {this.renderMenu()}
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  login: state.login,
  user: state.user,
});
const mapDispatchToProps = (dispatch) => ({
  onToggleDrawer: () => {
    dispatch(toggleDrawer());
  },
  onLogout: () => {
    dispatch(logout());
  },
  onlogoutUser: () => {
    dispatch(logoutUser());
  },
});
const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withStyles(style), withConnect)(Header);
