import { takeLatest, call, put, delay } from "redux-saga/effects";
import * as loginTypes from "../constant/LoginActions";
import * as loginActions from "../actions/LoginActions";
import * as loginApi from "../api/loginApi";
import { STATUS_CODE } from "../constant";
import * as courseTypes from "../constant/Course";
import * as courseActions from "../actions/CourseActions";
import * as courseApi from "../api/courseApi";
function* login({ payload }) {
  const { user } = payload;
  try {
    const response = yield call(loginApi.login, user);
    const { status, data } = response;
    if (status === STATUS_CODE.SUCCESS) {
      yield put(loginActions.loginSuccess(data.accessToken));
      const response2 = yield call(loginApi.fetchUser);
      const { status: status1, data: data1 } = response2;
      if (status1 === STATUS_CODE.SUCCESS) {
        yield put(loginActions.fetchUserSuccess(data1));
      }
    }
  } catch (e) {
    yield put(loginActions.loginFailed(e));
  }
}
function* fetchCourse() {
  try {
    const response = yield call(courseApi.fetchCourse);
    const { status, data } = response;
    if (status === STATUS_CODE.SUCCESS) {
      yield delay(1000);
      yield put(courseActions.fetchCourseSuccess(data));
    }
  } catch (e) {
    yield put(courseActions.fetchCourseFailed(e));
  }
}
function* fetchQuestion({ payload }) {
  const { courseid } = payload;
  try {
    const response = yield call(courseApi.fetchQuestion, courseid);
    const { status, data } = response;
    if (status === STATUS_CODE.SUCCESS) {
      yield put(courseActions.fetchQuestionSuccess(data));
    }
  } catch (e) {
    yield put(courseActions.fetchQuestionFailed(e));
  }
}
function* fetchOneCourse({ payload }) {
  const { courseid } = payload;
  try {
    const response = yield call(courseApi.fetchOneCourse, courseid);
    const { status, data } = response;
    if (status === STATUS_CODE.SUCCESS) {
      yield put(courseActions.fetchOneCourseSuccess(data));
    }
  } catch (e) {
    yield put(courseActions.fetchOneCourseFailed(e));
  }
}
function* checkTest({ payload }) {
  const { courseid, form } = payload;
  try {
    const response = yield call(courseApi.checkTest, form, courseid);
    const { status, data } = response;
    if (status === STATUS_CODE.SUCCESS) {
      yield put(courseActions.checkTestSuccess(data));
    }
  } catch (e) {
    yield put(courseActions.checkTestFailed(e));
  }
}
function* rootSaga() {
  yield takeLatest(loginTypes.LOGIN, login);
  yield takeLatest(courseTypes.FETCH_COURSE, fetchCourse);
  yield takeLatest(courseTypes.FETCH_QUESTION, fetchQuestion);
  yield takeLatest(courseTypes.FETCH_ONE_COURSE, fetchOneCourse);
  yield takeLatest(courseTypes.CHECK_TEST, checkTest);
}
export default rootSaga;
