import {
  Button,
  Grid,
  IconButton,
  InputAdornment,
  TextField,
  Typography,
  withStyles,
} from "@material-ui/core";
import VpnKeyIcon from "@material-ui/icons/VpnKey";
import { AccountCircle, Visibility, VisibilityOff } from "@material-ui/icons";
import React, { Component } from "react";
import { compose } from "redux";
import style from "./style";
import { login } from "../../actions/LoginActions";
import { connect } from "react-redux";
class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userInfor: {
        username: "",
        password: "",
      },
      showPassword: false,
    };
  }
  handleClickShowPassword = () => {
    this.setState({
      showPassword: !this.state.showPassword,
    });
  };

  handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  onLogin = () => {
    const { userInfor } = this.state;
    this.props.onLogin(userInfor);
  };
  onChange = (event) => {
    const field = event.target.name;
    const { userInfor } = this.state;
    userInfor[field] = event.target.value;
    this.setState({
      userInfor: userInfor,
    });
  };
  render() {
    const { classes } = this.props;
    const { showPassword, userInfor } = this.state;
    const { username, password } = userInfor;
    return (
      <div className={classes.background}>
        <div className={classes.form}>
          <form>
            <Grid>
              <Typography
                className={classes.title}
                variant="h4"
                color="primary"
              >
                Đăng nhập
              </Typography>
            </Grid>
            <Grid item>
              <TextField
                placeholder="Username"
                name="username"
                id="username"
                label="Username"
                value={username}
                onChange={this.onChange}
                className={classes.field}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AccountCircle />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid>
              <TextField
                placeholder="Password"
                type={showPassword ? "text" : "password"}
                id="password"
                name="password"
                label="Password"
                className={classes.field}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <VpnKeyIcon />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={this.handleClickShowPassword}
                      onMouseDown={this.handleMouseDownPassword}
                    >
                      {showPassword ? <Visibility /> : <VisibilityOff />}
                    </IconButton>
                  ),
                }}
                onChange={this.onChange}
              />
            </Grid>
            <Grid className={classes.button}>
              <Button
                variant="contained"
                className={classes.butChild}
                onClick={this.onLogin}
              >
                Đồng ý
              </Button>
            </Grid>
          </form>
        </div>
      </div>
    );
  }
}
const mapDispatchToProps = (dispatch) => ({
  onLogin: (userInfor) => {
    dispatch(login(userInfor));
  },
});
const withConnect = connect(null, mapDispatchToProps);
export default compose(withStyles(style), withConnect)(Login);
