import { Grid, Typography, withStyles } from '@material-ui/core';
import React, { useState } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import ControlResult from '../ControlResult';
import ResutlItem from "../ResutlItem"
import style from "./style"
function Result(props) {
const [order,setOrder]=useState(1)
const {results,courseid,classes}=props;
function onChangeOrder(order){
setOrder(order)
}
function onChangeOrder2(number){
    setOrder(order+number)
}
 function renderQuestion(){
    const { courseid } =props.match.params;
    return (
      <ResutlItem
        order={order}
        onChangeOrder={onChangeOrder2}
        courseid={courseid}
        results={props.results}
      />
    );
  };
    return (
        <div>
        <Grid container className={classes.root} spacing={2}>
          <Grid item md={7}>
            <Typography variant="h3" style={{ color: "#2E2EFE" }}>
              Test 1
            </Typography>
            <Typography>Bạn đã hoàn thành bài kiểm tra</Typography>
            <Typography>Dưới đây là kết quả của bạn</Typography>

            <Typography
              variant="h5"
              style={{ color: "#2E2EFE", marginBottom: 30, marginTop: 10 }}
            >
              Chúc bạn làm thành công ^^{" "}
            </Typography>
            {renderQuestion()}
          </Grid>
          <Grid item md={5}>
            <ControlResult
              length={results.length ? results.length : 0}
              order={order}
              onChangeOrder={onChangeOrder}
              results={results}
              courseid={courseid}
            />
          </Grid>
        </Grid>
      </div>
    );
}
const mapStateToProps=(state)=>({
    results:state.results
})
const mapDispatchToProps=(dispatch)=>({

})
const withConnect=connect(mapStateToProps,mapDispatchToProps);
export default compose(withConnect,withStyles(style))(Result);