import {
  Button,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Grid,
  Typography,
  withStyles,
} from "@material-ui/core";
import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { compose } from "redux";
import style from "./style";
function CourseList(props) {
  const [redirect, setRedirect] = useState(false);
  const [redirectTest, setRedirectTest] = useState(false);
  const { course, classes } = props;
  function onRedirect() {
    setRedirect(true);
  }
  function renderTest() {
    setRedirectTest(true);
  }
  return (
    <Grid item md={3}>
      {redirect ? <Redirect to={`/course/${course.courseid}`} /> : ""}
      {redirectTest ? <Redirect to={`/test/${course.courseid}`} /> : ""}
      <Card>
        <CardActionArea onClick={onRedirect}>
          <CardMedia
            className={classes.media}
            image={course.image}
            title="Contemplative Reptile"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              {course.title}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              {course.shortdescription}
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions className={classes.actions}>
          <Button
            size="small"
            style={{ color: "#088A08" }}
            variant="contained"
            className={classes.btn}
            onClick={onRedirect}
          >
            Học tập
          </Button>
          <Button
            size="small"
            style={{ color: "#FF0040" }}
            variant="contained"
            className={classes.btn}
            onClick={renderTest}
          >
            Kiểm tra
          </Button>
        </CardActions>
      </Card>
    </Grid>
  );
}

export default compose(withStyles(style))(CourseList);
