import * as types from "../../constant/Course";
export const fetchCourse = () => ({
  type: types.FETCH_COURSE,
});
export const fetchCourseSuccess = (courses) => ({
  type: types.FETCH_COURSE_SUCCESS,
  payload: {
    courses,
  },
});
export const fetchCourseFailed = (error) => ({
  type: types.FETCH_COURSE_FAILED,
  payload: {
    error,
  },
});
export const fetchQuestion = (courseid) => ({
  type: types.FETCH_QUESTION,
  payload: {
    courseid,
  },
});
export const fetchQuestionSuccess = (questions) => ({
  type: types.FETCH_QUESTION_SUCCESS,
  payload: {
    questions,
  },
});
export const fetchQuestionFailed = (error) => ({
  type: types.FETCH_QUESTION_FAILED,
  payload: {
    error,
  },
});
export const fetchOneCourse = (courseid) => ({
  type: types.FETCH_ONE_COURSE,
  payload: {
    courseid,
  },
});
export const fetchOneCourseSuccess = (course) => ({
  type: types.FETCH_ONE_COURSE_SUCCESS,
  payload: {
    course,
  },
});
export const fetchOneCourseFailed = (error) => ({
  type: types.FETCH_ONE_COURSE_FAILED,
  payload: {
    error,
  },
});
export const checkTest = (form, courseid) => ({
  type: types.CHECK_TEST,
  payload: {
    form,
    courseid,
  },
});
export const checkTestSuccess = (results) => ({
  type: types.CHECK_TEST_SUCCESS,
  payload: {
    results,
  },
});
export const checkTestFailed = (error) => ({
  type: types.CHECK_TEST_FAILED,
  payload: {
    error,
  },
});
