import {
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  Typography,
  withStyles,
} from "@material-ui/core";
import React, { Component } from "react";
import { compose } from "redux";
import BuildIcon from "@material-ui/icons/Build";
import HomeIcon from "@material-ui/icons/Home";
import style from "./style";
import MenuBookIcon from "@material-ui/icons/MenuBook";
import { connect } from "react-redux";
import MenuLink from "../FormHelper/RenderLink/renderLink";
import AccountCircleOutlinedIcon from "@material-ui/icons/AccountCircleOutlined";
import PeopleOutlineSharpIcon from "@material-ui/icons/PeopleOutlineSharp";
import HelpOutlineSharpIcon from "@material-ui/icons/HelpOutlineSharp";
import { toggleDrawer } from "../../actions/DrawersActions";
import CalendarTodayIcon from "@material-ui/icons/CalendarToday";
class Drawer1 extends Component {
  toggleDrawer = (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    this.props.onToggleDrawer();
  };
  showList = () => {
    const { classes } = this.props;
    return (
      <div
        className={classes.list}
        onClick={this.toggleDrawer}
        onKeyDown={this.toggleDrawer}
      >
        <List>
          <Typography variant="h5" className={classes.menuText}>
            Thông tin chi tiết
          </Typography>

          <ListItem>
            <ListItemIcon>
              {" "}
              <HomeIcon />
            </ListItemIcon>
            <MenuLink
              to="/"
              label="Trang chủ"
              activeOnlyWhenExact={true}
              className={classes.link}
            />
          </ListItem>
          <ListItem>
            <ListItemIcon>
              <AccountCircleOutlinedIcon />
            </ListItemIcon>
            <MenuLink
              to="/my-profile"
              label="Profile"
              activeOnlyWhenExact={true}
              className={classes.link}
            />
          </ListItem>
          <ListItem>
            <ListItemIcon>
              {" "}
              <MenuBookIcon />
            </ListItemIcon>
            <MenuLink
              to="/course"
              label="Course"
              activeOnlyWhenExact={true}
              className={classes.link}
            />
          </ListItem>
        </List>
      </div>
    );
  };
  render() {
    const { classes, open } = this.props;
    return (
      <div>
        <Drawer open={open} onClose={this.toggleDrawer}>
          {this.showList()}
        </Drawer>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  open: state.drawer,
});
const mapDispatchToProps = (dispatch) => ({
  onToggleDrawer: () => {
    dispatch(toggleDrawer());
  },
});
const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withStyles(style), withConnect)(Drawer1);
