import React, { Component } from "react";
import { LOGIN_ROUTE, USER_ROUTE } from "./constant";
import { BrowserRouter, Switch } from "react-router-dom";
import { ThemeProvider } from "@material-ui/core";
import { ToastContainer } from "react-toastify";
import LoginRoute from "./common/Layout/LoginRoute";
import "react-toastify/dist/ReactToastify.css";
import theme from "./common/Theme";
import UserRoute from "./common/Layout/UserRoute";
class App extends Component {
  renderLogin = () => {
    let xhtml = LOGIN_ROUTE.map((item) => {
      return (
        <LoginRoute
          key={item.path}
          component={item.component}
          path={item.path}
          exact={item.exact}
        />
      );
    });
    return xhtml;
  };
  renderUserRoute = () => {
    let xhtml = USER_ROUTE.map((item) => {
      return (
        <UserRoute
          key={item.path}
          component={item.component}
          path={item.path}
          exact={item.exact}
        />
      );
    });
    return xhtml;
  };
  render() {
    console.log("start");
    return (
      <BrowserRouter>
        <ThemeProvider theme={theme}>
          <ToastContainer />
          <Switch>
            {this.renderLogin()}
            {this.renderUserRoute()}
          </Switch>
        </ThemeProvider>
      </BrowserRouter>
    );
  }
}
export default App;
