import { Grid, Typography, withStyles } from "@material-ui/core";
import React, { useEffect } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { fetchCourse } from "../../actions/CourseActions";
import CourseItem from "../CourseItem";
import style from "./style";
import Skeleton from "@material-ui/lab/Skeleton";
function CourseList(props) {
  const { courses } = props;
  function renderCourse() {
    let xhtml = null;
    if (courses.length > 0) {
      xhtml = courses.map((item, index) => {
        return <CourseItem key={index} course={item} />;
      });
    }
    return xhtml;
  }
  useEffect(() => {
    props.fetchCourses();
  }, []);
  function renderSkeleton() {
    var arr = [1, 2, 3, 4];
    let xhtml = arr.map((item, index) => {
      return (
        <Grid item md={3}>
          <Skeleton variant="rect" width="100%" height={200} />
          <Typography component="div" key={item} variant="h4">
            <Skeleton variant="text" width="70%" />
          </Typography>
          <Skeleton variant="text" />
        </Grid>
      );
    });
    return xhtml;
  }
  return (
    <Grid container spacing={3} style={{ marginTop: 5 }}>
      {courses.length > 0 ? renderCourse() : renderSkeleton()}
    </Grid>
  );
}
const mapStateToProps = (state) => ({
  courses: state.courses,
});
const mapDispatchToProps = (dispatch) => ({
  fetchCourses: () => {
    dispatch(fetchCourse());
  },
});
const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withStyles(style), withConnect)(CourseList);
