import { toastError } from "../common/ToastService";
import * as types from "../constant/LoginActions";
const initial = !!sessionStorage.jwt;
const loginUser = (state = initial, actions) => {
  switch (actions.type) {
    case types.LOGIN:
      return state;
    case types.LOGIN_SUCCESS:
      var { jwt } = actions.payload;
      sessionStorage.setItem("jwt", jwt);
      console.log(jwt);
      return !!sessionStorage.jwt;
    case types.LOGIN_FAILED:
      toastError("Tên đăng nhập hoặc mật khẩu không chính xác");
      return state;
    case types.LOGOUT:
      sessionStorage.removeItem("jwt");
      return false;
    default:
      return state;
  }
};
export default loginUser;
