import * as types from "../constant/LoginActions";
var data = JSON.parse(localStorage.getItem("user"));
const initial = data
  ? data
  : {
      username: "",
      password: "",
      code: "",
    };
const userReducer = (state = initial, actions) => {
  switch (actions.type) {
    case types.FETCH_USER:
      return { ...state };
    case types.FETCH_USER_SUCCESS:
      var { user } = actions.payload;
      localStorage.setItem("user", JSON.stringify(user));
      return user;
    case types.LOGOUT_USER:
      localStorage.removeItem("user");
      return {
        username: "",
        password: "",
        roleDTO: "",
      };
    case types.FETCH_USER_FAILED:
      return { ...state };

    default:
      return {
        ...state,
      };
  }
};
export default userReducer;
