import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect, Route } from "react-router";
import { compose } from "redux";
import LoginPage from "../../../page/LoginPage";
class LoginRoute extends Component {
  renderAfter = (path) => {
    const { login, user } = this.props;
    const role = user.code;
    if (login && role === "ROLE_ADMIN") {
      return <Redirect to="/admin" />;
    } else if (login && role === "ROLE_USER") {
      return <Redirect to="/" />;
    } else {
      return <LoginPage />;
    }
  };
  render() {
    const { component: YourComponent, path, exact } = this.props;
    console.log("AA");
    return (
      <Route
        path={path}
        exact={exact}
        render={(routeProps) => {
          return <YourComponent {...routeProps} />;
        }}
      >
        {this.renderAfter(path)}
      </Route>
    );
  }
}
const mapStateToProps = (state) => ({
  user: state.user,
  login: state.login,
});
const withConnect = connect(mapStateToProps, null);
export default compose(withConnect)(LoginRoute);
