import {
  Button,
  CardMedia,
  FormControlLabel,
  Grid,
  Radio,
  Typography,
  withStyles,
} from "@material-ui/core";
import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { compose } from "redux";
import { Field, reduxForm, reset } from "redux-form";
import style from "./style";
import RenderRadio from "../FormHelper/RenderRadio";
import { connect } from "react-redux";
import { checkTest } from "../../actions/CourseActions";
function CourseList(props) {
  const { questions, order, classes, courseid } = props;
  const [redirect, setRedirect] = useState(false);
  function renderImage(question) {
    if (question && question.image) {
      return (
        <CardMedia
          image={`/${question.image}`}
          title="image"
          style={{ marginTop: 6, height: 300 }}
        ></CardMedia>
      );
    }
  }
  function renderQuestion() {
    let xhtml = null;
    if (questions.length > 0) {
      xhtml = questions.map((item, index) => {
        return (
          <Grid
            key={index}
            style={{ display: item.numberorder === order ? "" : "none" }}
          >
            <Typography style={{ marginBottom: 30 }}>
              <b>{item ? item.title : ""}</b>
            </Typography>
            {renderImage(item)}

            <Typography style={{ marginBottom: 15, marginTop: 15 }}>
              <b>Question {item.numberorder} :</b>
            </Typography>
            <Field
              id={`testQuestion${item.numberorder}`}
              name={`testQuestion${item.numberorder}`}
              component={RenderRadio}
              style={{ marginLeft: 20 }}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                label={`A: ${item.option1}`}
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label={`B: ${item.option2}`}
              />
              <FormControlLabel
                value="3"
                control={<Radio />}
                label={`C: ${item.option3}`}
              />
              <FormControlLabel
                value="4"
                control={<Radio />}
                label={`D: ${item.option4}`}
              />
            </Field>
          </Grid>
        );
      });
    }
    return xhtml;
  }
  function handleFormSubmit(form) {
    var formData = [];
    for (var i in form) {
      formData.push({
        numberorder: parseInt(i.substring(12)),
        youranswer: parseInt(form[i]),
      });
    }
    props.onCheckResult(formData, courseid);
    setRedirect(true);
  }
  function onChangeOrder(number) {
    props.onChangeOrder(number);
  }
  function resetForm() {
    props.reset();
    props.toggleReset();
  }
  const {
    handleSubmit,
    invalid,
    submitting,

    resetFormData,

    complete,
  } = props;
  const length = questions.length;
  return (
    <form onSubmit={handleSubmit(handleFormSubmit)} className={classes.border}>
      {redirect ? <Redirect to={`/result/${courseid}`} /> : ""}
      {renderQuestion()}
      {complete ? handleFormSubmit() : ""}
      {resetFormData ? resetForm() : ""}
      <Grid className={classes.rootBtn}>
        {order !== 1 ? (
          <Button
            variant="outlined"
            className={classes.btn}
            onClick={() => {
              onChangeOrder(-1);
            }}
          >
            Back
          </Button>
        ) : (
          ""
        )}
        {order !== length ? (
          <Button
            className={classes.btn}
            variant="outlined"
            onClick={() => {
              onChangeOrder(1);
            }}
          >
            Next
          </Button>
        ) : (
          ""
        )}
        <Button variant="outlined" type="submit" className={classes.btn}>
          Chấm điểm
        </Button>
      </Grid>
    </form>
  );
}
const mapStateToProps = (state) => ({});
const mapDispatchToProps = (dispatch) => ({
  reset: () => {
    dispatch(reset(FORMN_NAME));
  },
  onCheckResult: (form, courseid) => {
    dispatch(checkTest(form, courseid));
  },
});
const withConnect = connect(mapStateToProps, mapDispatchToProps);
const FORMN_NAME = "TEST_FORM";
const withReduxForm = reduxForm({
  form: FORMN_NAME,
  enableReinitialize: true,
});
export default compose(
  withStyles(style),
  withReduxForm,
  withConnect
)(CourseList);
