import { Grid, Typography, withStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { compose } from "redux";
import style from "./style";
import ControlTest from "../ControlTest/index";
import QuestionItem from "../QuestionItem";
import { connect } from "react-redux";
import { fetchOneCourse, fetchQuestion } from "../../actions/CourseActions";
function CourseList(props) {
  const { courseid } = props.match.params;
  const [order, setOrder] = useState(1);
  const [resetForm, setResetForm] = useState(false);
  const [complete, setComplete] = useState(false);
  const [pause, setPause] = useState(false);
  const { questions, classes, course } = props;
  useEffect(() => {
    props.fetchQuestion(courseid);
    props.fetchCourse(courseid);
  }, []);
  function renderQuestion() {
    return (
      <QuestionItem
        resetFormData={resetForm}
        questions={questions}
        order={order}
        onChangeOrder={onChangeOrder2}
        courseid={courseid}
        complete={complete}
        toggleReset={toggleReset}
      />
    );
  }
  function toggleReset() {
    setResetForm(false);
  }
  function onChangeOrder2(number) {
    setOrder(order + number);
  }

  function onRework() {
    setOrder(1);
    setPause(false);
    setResetForm(true);
  }

  function onChangeOrder(number) {
    setOrder(number);
  }
  function onComplete() {
    setComplete(true);
  }
  return (
    <div>
      {console.log("course", course)}
      <Grid container className={classes.root} spacing={2}>
        <Grid item md={7}>
          <Typography variant="h3" style={{ color: "#2E2EFE" }}>
            Test 1
          </Typography>
          <Typography>Bạn có 120 phút để làm bài</Typography>
          <Typography>Khi làm xong bạn ấn nộp bài để xem kết quả</Typography>
          <Typography>Hết thời gian hệ thống sẽ tự động nộp bài</Typography>
          <Typography
            variant="h5"
            style={{ color: "#2E2EFE", marginBottom: 30, marginTop: 10 }}
          >
            Chúc bạn làm bài tốt{" "}
          </Typography>
          {renderQuestion()}
        </Grid>
        <Grid item md={5}>
          <ControlTest
            onRework={onRework}
            length={questions.length ? questions.length : 0}
            order={order}
            onChangeOrder={onChangeOrder}
            pause={pause}
            time={course ? course.time * 1000 * 60 : 10000}
            onComplete={onComplete}
          />
        </Grid>
      </Grid>
    </div>
  );
}
const mapStateToProps = (state) => ({
  questions: state.questions,
  course: state.course,
});
const mapDispatchToProps = (dispatch) => ({
  fetchQuestion: (courseid) => {
    dispatch(fetchQuestion(courseid));
  },
  fetchCourse: (courseid) => {
    dispatch(fetchOneCourse(courseid));
  },
});
const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withStyles(style), withConnect)(CourseList);
