import * as types from "../constant/Course";
const initial = [];
const resultsRecuer = (state = initial, actions) => {
  switch (actions.type) {
    case types.CHECK_TEST:
      return [...state];
    case types.CHECK_TEST_SUCCESS:
      const { results } = actions.payload;
      return results;
    case types.CHECK_TEST_FAILED:
      return [...state];
    default:
      return [...state];
  }
};
export default resultsRecuer;
