import * as types from "../constant/Course";
const inital = [];
const quesionsReducer = (state = inital, actions) => {
  switch (actions.type) {
    case types.FETCH_QUESTION:
      return [...state];
    case types.FETCH_QUESTION_SUCCESS:
      const { questions } = actions.payload;
      return questions;
    case types.FETCH_QUESTION_FAILED:
      return [...state];
    default:
      return [...state];
  }
};
export default quesionsReducer;
