import { Grid } from "@material-ui/core";
import React, { Fragment } from "react";
import CourseList from "../../components/CourseList";
import Drawer from "../../components/Drawer";
import Header from "../../components/Header";
import { Container, Row, Col, Image } from "react-bootstrap";
// import Exercise from "./Exercise";
import "bootstrap/dist/css/bootstrap.min.css";
// import "./style.css";
function HomePage(props) {
  return (
    <Fragment>
      <Header />
      <Drawer />
      {/* <Header />
      <Drawer />
      <Grid container>
        <Grid item md={1}></Grid>
        <Grid item md={10}>

        </Grid>
        <Grid item md={1}></Grid>
      </Grid> */}
      <div style={{ marginTop: 10 }}>
        <Container>
          <Row>
            <Col sm={8} style={{ backgroundColor: "#ededed" }}>
              <h2>
                7 ứng dụng học lập trình online cho trẻ em cực kỳ hiệu quả ngay
                tại nhà
              </h2>
              <p>
                Thế giới công nghệ ngày càng phát triển, con người càng bị phụ
                thuộc nhiều vào công nghệ. Chính vì vậy, để trang bị cho các em
                những kiến thức cũng như kỹ năng làm sao để có thể hòa nhập được
                với xu hướng của thế giới, rất nhiều các bậc phụ huynh đã cho
                con em mình đi theo con đường lập trình. Và để trẻ có thể tiếp
                cận với lập trình, học lập trình một cách hiệu quả đa số các bậc
                phụ huỳnh đã lựa chọn phương pháp học lập trình online cho trẻ
                em qua 7 ứng dụng tại nhà dưới đây. Chúng ta hãy cùng tìm hiểu
                nhé!
              </p>
              <h4>1. Ứng dụng Kodable</h4>
              <div>
                <Image
                  src="https://resources.mindx.edu.vn/uploads/images/hoc-lap-trinh-online-cho-tre-em-01.png"
                  fluid
                />
              </div>
              <p>
                Tuy nhiên, Kodable không để các khái niệm lập trình quá đơn
                giản, nó thậm chí còn đưa ra các khái niệm về vòng lặp loop và
                kỹ thuật phân nhánh (branching). Mà chủ yếu là đưa ra các khái
                niệm lập trình ban đầu, giúp trẻ có cái nhìn sơ bộ về lập trình,
                viết code.
              </p>
              <p>
                Ngoài ra, ứng dụng khiến việc học ngôn ngữ lập trình JavaScript
                trở nên thú vị hơn bằng cách sử dụng gamification (game hóa).
                Nhìn chung, Kodable là ứng dụng “nhập môn” về lập trình.
              </p>
              <h4>2. Ứng dụng Daisy the Dinosaur</h4>
              <p>
                Daisy the Dinosaur là một trò chơi về lập trình và là một trong
                những ứng dụng học code tốt nhất đối với trẻ em. Các mini game
                trong ứng dụng sẽ dạy trẻ những điều cơ bản về lập trình.
              </p>
              <p>
                Điều khiến Daisy the Dinosaur là một trong những ứng dụng học
                lập trình online cho trẻ em hiệu quả nhất và được yêu thích nhất
                chính nhân vật chú khủng long – con vật ưa thích của trẻ em mặc
                dù nó tập trung vào việc mã hóa và những thách thức nhiều hơn.
              </p>
              <div>
                <Image
                  src="https://resources.mindx.edu.vn/uploads/images/hoc-lap-trinh-online-cho-tre-em-02.png"
                  fluid
                />
              </div>
              <p>
                Bên cạnh đó, ứng dụng Daisy the Dinosaur dường như không có công
                nghệ, nó chỉ là một trò chơi giải câu đố và từ đơn giản. Chúng
                hoàn toàn miễn phí, không có nhiều kỹ thuật và vô cùng phù hợp
                với trẻ em.
              </p>
              <h4>3. Ứng dụng Think and Learn Code-a-Pillar</h4>
              <p>
                The Think and Learn Code-a-Pillar của Fisher-Price là sự kết hợp
                tuyệt vời giữa các trò chơi và ứng dụng và đặc biệt bạn có thể
                sử dụng ngoại tuyến. Ứng dụng được phát triển nhằm vào lứa tuổi
                từ 3 đến 6 tuổi.
              </p>
              <p>
                Với ứng dụng này, trẻ sẽ tự giải quyết các câu đố liên quan đến
                <a href="https://mindx.edu.vn/blog/post/hoc-lap-trinh-web-co-ban">
                  {" "}
                  khái niệm cơ bản về lập trình máy tính
                </a>{" "}
                và mã hóa. Mặc dù nó có trò chơi Code-a-Pillar toy nhưng ứng
                dụng hoàn toàn độc lập. Vì vậy tốt nhất người lớn nên giám sát
                trẻ khi sử dụng ứng dụng. Ngoài ra, bạn có thể tắt âm nhạc và
                hiệu ứng để tránh trẻ phân tâm khi sử dụng.
              </p>
              <Image
                src="https://resources.mindx.edu.vn/uploads/images/hoc-lap-trinh-online-cho-tre-em-04.jpeg"
                fluid
              />
              <h4>4. Ứng dụng Gamestar Mechanic</h4>
              <p>
                Gamestar Mechanic là một ứng dụng dựa trên web dạy cho trẻ em tự
                làm các trò chơi video của riêng mình. Thực tế cho thấy, việc
                thiết kế trò chơi hấp dẫn trẻ em hơn là việc phát triển web và
                ứng dụng. Điều này không những giúp trẻ phát triển kỹ năng mà
                còn tự tạo được trò chơi của riêng mình.Tuy nhiên vì Gamestar
                Mechanic tập trung vào thiết kế trò chơi nên nó tương đối khó.
                Với trẻ khoảng 7-14 tuổi, Gamestar Mechanic là ứng dụng hoàn
                hảo.
              </p>
              <Image
                src="https://resources.mindx.edu.vn/uploads/images/hoc-lap-trinh-online-cho-tre-em-04.jpg"
                fluid
              />
              <p>
                Ứng dụng bao gồm các khóa học, sáng tạo trò chơi và các tính
                năng vừa học vừa chơi với gamification. Việc mà trẻ cần thực
                hiện là các nhiệm vụ trong trò chơi và dành được những item để
                tạo trò chơi. Ứng dụng có một cộng đồng hỗ trợ mạnh mẽ, khiến nó
                trở thành ứng dụng học lập trình online cho trẻ em tuyệt vời
                nhất là đối với lứa tuổi thanh thiếu niên.
              </p>
              <h4>5. Ứng dụng Minecraft</h4>
              <p>
                Minecraft là một trò chơi tương đối phổ biến vì nó được thiết kế
                theo phong cách “hộp cát” (sandbox). Mặc dù nó không được phát
                triển cho trẻ em nhưng Minecraft và nội dung của nó cung cấp môi
                trường an toàn cho lập trình.
              </p>
              <p>
                Trong trò chơi có một số mod giành riêng cho trẻ em như
                LearnToMod. Bạn có thể sử dụng Minecraft để dạy trẻ về máy chủ
                và thiết lập một máy chủ trò chơi Linux. Thực tế Linux rất thích
                hợp với trẻ em và dạy chúng nhiều bài học cuộc sống giá trị.
              </p>
              <Image
                src="https://resources.mindx.edu.vn/uploads/images/hoc-lap-trinh-online-cho-tre-em-06.png"
                fluid
              />
              <p>
                Điểm hạn chế của Minecraft là không được định cấu hình trước cho
                trẻ em. Vậy nên, người lớn cần phải thực hiện một số thiết lập
                ban đầu. Sau khi đã thiết lập xong, LearnToMod cung cấp một
                lượng kiến thức lập trình cơ bản.
              </p>
              <h4>6. Ứng dụng Tynker</h4>
              <p>
                Tynker truyền tải mã hóa với niềm vui thích. Tại đây, trẻ em có
                thể code robot và máy bay không người lái, mod Minecraft, xây
                dựng các ứng dụng và trò chơi, hoặc khám phá STEM.
              </p>
              <p>
                Trẻ có thể bắt đầu bằng việc lập trình sử dụng khối hình ảnh
                trước khi bắt đầu với{" "}
                <a href="https://mindx.edu.vn/blog/post/python-la-gi">Python</a>{" "}
                và JavaScript. Chương trình tập trung vào các dự án vì vậy có
                một đề xuất giá trị hấp dẫn dành cho trẻ em. Bởi vì Tynker bắt
                đầu với các khối hình ảnh trước khi chuyển sang lập trình thực
                tế. Một môi trường toàn diện với mức độ tiến bộ ngày càng tăng
                làm cho Tynker trở thành một trong những phương tiện tốt nhất để
                trẻ học lập trình.
              </p>
            </Col>
            <Col sm={4} style={{ backgroundColor: "#ededed" }}>
              <div style={{ position: "sticky", top: "0" }}>
                <h4
                  style={{
                    color: "white",
                    backgroundColor: "#afafaf",
                    padding: "3px",
                    textAlign: "center",
                  }}
                >
                  Đừng bỏ lỡ
                </h4>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </Fragment>
  );
}

export default HomePage;
