import * as types from "./../constant/Course";
const initial = {};
const courseReducer = (state = initial, actions) => {
  switch (actions.type) {
    case types.FETCH_ONE_COURSE:
      return { ...state };
    case types.FETCH_ONE_COURSE_SUCCESS:
      const { course } = actions.payload;
      return course;
    case types.FETCH_ONE_COURSE_FAILED:
      return { ...state };
    default:
      return { ...state };
  }
};
export default courseReducer;
