import { API_URL } from "../constant";
import AxiosService from "../common/Service/AxiosService";
export const requestHeaders = () => {
  return { AUTHORIZATION: `Bearer ${sessionStorage.jwt}` };
};
export const fetchCourse = () => {
  const api = `${API_URL}/course`;
  const headers = requestHeaders();
  return AxiosService.get(api, { headers });
};
export const fetchQuestion = (courseid) => {
  const api = `${API_URL}/test/${courseid}`;
  const headers = requestHeaders();
  return AxiosService.get(api, { headers });
};
export const fetchOneCourse = (courseid) => {
  const api = `${API_URL}/course/${courseid}`;
  const headers = requestHeaders();
  return AxiosService.get(api, { headers });
};
export const checkTest = (form, courseid) => {
  const api = `${API_URL}/test/${courseid}`;
  const headers = requestHeaders();
  return AxiosService.post(api, form, { headers });
};
