import { Grid } from "@material-ui/core";
import React, { Fragment } from "react";
import CourseList from "../../components/CourseList";
import Drawer from "../../components/Drawer";
import Header from "../../components/Header";
import { Container, Row, Col, Image } from "react-bootstrap";
// import Exercise from "./Exercise";
import "bootstrap/dist/css/bootstrap.min.css";
// import "./style.css";
function HomePage(props) {
  return (
    <Fragment>
      <Header />
      <Drawer />
      {/* <Header />
      <Drawer />
      <Grid container>
        <Grid item md={1}></Grid>
        <Grid item md={10}>

        </Grid>
        <Grid item md={1}></Grid>
      </Grid> */}
      <div style={{ marginTop: 10 }}>
        <Container>
          <Row>
            <Col sm={8} style={{ backgroundColor: "#ededed" }}>
              <h2>Top 10 ngôn ngữ lập trình cho trẻ em</h2>
              <br />
              <p>
                Bạn đang tìm kiếm một loại ngôn ngữ lập trình phù hợp với trẻ em
                để làm quen với lập trình, nâng cao tư duy logic thì hãy tham
                khảo top 10 ngôn ngữ lập trình cho trẻ em sau đây:
              </p>
              <h4>Alice</h4>
              <div style={{ textAlign: "center" }}>
                <Image
                  src="http://bachkhoa-npower.vn/wp-content/uploads/2019/07/alice.png"
                  fluid
                />
              </div>
              <p>
                Alice là một dạng ngôn ngữ lập trình có nền tảng giáo dục tốt,
                dựa trên đối tượng nguồn mở, có môi trường phát triển tích hợp
                IDE. Đây là một loại ngôn ngữ lập trình miễn phí, dùng để chỉ
                dạy và hướng dẫn các khái niệm lập trình, hướng đối tượng như
                C++. Alice cho phép trẻ em tạo ra các trò chơi hoặc hình ảnh
                bằng cách lập trình các chuyển động của máy ảnh, các mô hình 3D
                và các cảnh bằng cách tiếp cận quen thuộc của các hình khối xây
                dựng. Điều này giúp trẻ nâng cao hiệu quả “học đi đôi với hành”.
                Không chỉ dừng lại ở đối tượng là trẻ em mà Alice còn cung cấp
                các công cụ và tài liệu bổ sung cho việc giảng dạy ở nhiều lứa
                tuổi khác nhau.
              </p>
              <p>
                Alice cung cấp các kỹ năng tư duy logic và các nguyên tắc cơ bản
                trong ngôn ngữ lập trình như là trình tự, vòng lặp, Code, hướng
                dẫn các thao tác cụ thể để phát triển ứng dụng… Vì thế mà ứng
                dụng này thường dành cho trẻ em trên 12 tuổi. Ở độ tuổi này, trẻ
                cũng đã được tiếp xúc cơ bản với code nên việc làm quen với
                Alice sẽ không có quá nhiều bỡ ngỡ.
              </p>
              <h4>Blocky</h4>
              <div style={{ textAlign: "center" }}>
                <Image
                  src="http://bachkhoa-npower.vn/wp-content/uploads/2019/07/blockly.png"
                  fluid
                />
              </div>
              <p>
                Blockly là dự án của Google. Nóp là nguồn mở, thường chạy trong
                trình duyệt web và trông giống như Scratch. Blockly cũng là một
                ngôn ngữ lập trình thân thiện với trẻ em và một thư viện
                JavaScript phía máy khách để tạo ngôn ngữ lập trình và trình
                chỉnh sửa khối hình ảnh.
              </p>
              <p>
                Khi sử dụng ngôn ngữ này, trẻ sẽ thấy được mã dọc theo cạnh của
                màn hình khi liên kết các khối với nhau. Đồng thời có thể thay
                đổi ngôn ngữ lập trình để kiểm tra hay xem sự khác biệt về cú
                pháp ngôn ngữ cho cùng một chương trình cơ bản. Điều nay, giúp
                trẻ em dễ thao tác và xử lý các lỗi thường gặp.
              </p>
              <h4>Scratch</h4>
              <div style={{ textAlign: "center" }}>
                <Image
                  src="http://bachkhoa-npower.vn/wp-content/uploads/2019/07/scratch.png"
                  fluid
                />
              </div>
              <p>
                Có thể nói, Scratch là một trong những ngôn ngữ lập trình cho
                trẻ em khá đơn giản. Bởi vì đây là ngôn ngữ hình ảnh có đủ chức
                năng và các tùy chọn có thể tạo ra hình ảnh động, câu chuyện
                tương tác, nghệ thuật hoặc âm nhạc. Đặc biệt là Scratch có thể
                được hiểu hoàn toàn thông qua thao tác các biểu tượng đại diện
                cho các lệnh, vì thế trẻ em dễ sử dụng mà không cần phải tốn quá
                nhiều thời gian cho việc đọc và gõ. Hơn nữa, đây là ngôn ngữ lập
                trình miễn phí, giúp trẻ em có nhiều trải nghiệm, tạo nền tảng
                tốt để theo đuổi các loại ngôn ngữ khó hơn.
              </p>
              <h4>CoderZ</h4>
              <div style={{ textAlign: "center" }}>
                <Image
                  src="http://bachkhoa-npower.vn/wp-content/uploads/2019/07/coderz.png"
                  fluid
                />
              </div>
              <p>
                CoderZ được phát triển bởi công ty Interlitek tại Israel. Đây là
                môi trường trực tuyến để học STEM và học lập trình dành cho trẻ
                em thông qua việc lập trình một robot ảo thực hiện nhiệm vụ. Khi
                tiến bộ, trẻ em có thể học nhiều hơn về code Java cơ bản. Đây
                cũng là môi trường tuyệt vời, vừa là giải pháp vui và thú vị để
                rèn luyện kỹ năng trong thế giới công nghệ của thế kỷ 21.
              </p>
              <p>
                Bên cạnh học thêm về lập trình căn bản và lập trình robotics thì
                trẻ em còn có thể học thêm về toán, kỹ thuật, đồng thời cải
                thiện khả năng tư duy logic, tư duy thuật toán, rèn luyện năng
                lực giải quyết vấn đề cùng với tư duy sáng tạo, cải thiện trí
                nhớ hiệu quả.
              </p>
              <h4>Twine</h4>
              <p>
                Twine là phần mềm mã nguồn mở, hoàn toàn miễn phí giúp trẻ kể
                những câu chuyện của mình một cách sinh động hơn. Với Twine, trẻ
                không cần phải viết bất kỳ một đoạn code nào nhưng có thể mở
                rộng nó với các biến, điều kiện logic, hình ảnh, CSS và cả
                JavaScript, giúp trẻ tiếp cận và làm quan với các ngôn ngữ thiết
                kế web cơ bản này
              </p>
              <p>
                Giao diện của Twine cũng khá đơn giản, bao gồm một thanh trình
                đơn, một vài phím tắt và một bảng điều khiển để hiển thị tất cả
                các yếu tố hiện diện trong câu chuyện. Bất kì đứa trẻ nào cũng
                có thể sử dụng được Twine dù chưa từng sử dụng qua lần nào.
                Twine hỗ trợ nhập vào các định dạng như TWEE, TW, TXT, HTML và
                xuất sang các định dạng RTF, TXT hoặc TWEE. Tuy nhiên nếu không
                lựa chọn định dạng xuất, câu chuyện sẽ được lưu mặc định với
                định dạng HTML. Về phần hình ảnh trẻ có thể tải lên từ ổ đĩa (hỗ
                trợ định dạng GIF, JPG, PNG, SVG và WEBP) hoặc từ một URL cũng
                như CSS hay mã script. Vì thế, Twine là một sự lựa chọn tuyệt
                vời dành cho trẻ trong độ tuổi từ 5-8.
              </p>
              <h4>Python</h4>
              <div style={{ textAlign: "center" }}>
                <Image
                  src="http://bachkhoa-npower.vn/wp-content/uploads/2019/07/python.png"
                  fluid
                />
              </div>
              <p>
                Python được thiết kế để trở thành một ngôn ngữ dễ học cho trẻ
                em, có mã nguồn, bố cục trực quan và dễ hiểu. Vì thế sử dụng
                ngôn ngữ lập trình Python, trẻ em sẽ có ít trở ngại liên quan
                đến việc học cách lập trình. Ở ngôn ngữ lập trình này sẽ có
                nhiều chức năng phổ biến đã được tích hợp. Chính vì vậy ngôn ngữ
                lập trình Python trở thành một ngôn ngữ rất tuyệt vời để trẻ em
                tìm hiểu và học tập, phát triển trí thông minh của mình.
              </p>
              <h4>PHP</h4>
              <p>
                PHP là viết tắt của “Hypertext Preprocessor”. Đây là một trong
                những loại ngôn ngữ lập trình được sử dụng rộng rãi hiện nay.
                Đây là một loại ngôn ngữ lập trình kịch bản chạy ở phía server
                nhằm sinh ra mã html trên client. Cho đến hiện nay, PHP đã trải
                qua rất nhiều các phiên bản khác nhau và được tối ưu hóa cho các
                ứng dụng web. Cách viết mã rõ ràng, dễ học nên PHP đã dần trở
                thành một ngôn ngữ lập trình web rất được ưa chuộng. Vì thế, khi
                tuyển lập trình viên, các công ty thường yêu cần các ứng viên có
                vốn hiểu biết và sử dụng loại ngôn ngữ này một cách nhất định.
              </p>
              <p>
                PHP chạy trên môi trường webserver và lưu trữ dữ liệu thông qua
                hệ quản trị cơ sở dữ. Vậy nên PHP thường đi kèm với Apache, My
                SQL và hệ điều hành Linus (LAMP).
              </p>
              <h4>JavaScript</h4>
              <div style={{ textAlign: "center" }}>
                <Image
                  src="http://bachkhoa-npower.vn/wp-content/uploads/2019/07/javascript.png"
                  fluid
                />
              </div>
              <p>
                JavaScript thường bị nhầm là Java nhưng chúng là 2 loại ngôn ngữ
                lập trình khác nhau. JavaScript là ngôn ngữ lập trình hướng đối
                tượng, thông dịch đơn giản, được sử dụng trên hầu hết website
                ngày nay. Giống như HTML, PHP, trẻ muốn học thiết kế web thì nên
                tìm hiểu JavaScript. JavaScript là ngôn ngữ phổ biến nhất trên
                GitHub. Tuy nhiên, khi học JavaScript trẻ cần phải chịu khó, tỉ
                mỉ, có lộ trình rõ ràng, cũng không rất dễ bỏ cuộc.
              </p>
              <h4>Java</h4>
              <p>
                Java là ngôn ngữ lập trình hướng đối tượng được tạo bởi Sun
                Microsystems và hiện thuộc sự sở hữu của Oracle. Loại ngôn ngữ
                này thường được sử dụng cho database, phát triển ứng dụng
                Android, ứng dụng máy tính để bàn. Đây cũng là một ngôn ngữ lập
                trình phổ biến DND cho web. Để lập trình hệ thống lớn, người ta
                thường sử dụng ngôn ngữ Java. Java chạy nhanh hơn những ngôn ngữ
                thông dịch như Python, Perl, PHP gấp nhiều lần. Java chạy tương
                đương so với C#, một ngôn ngữ khá tương đồng về mặt cú pháp và
                quá trình dịch/chạy. Ngoài ra Java là một trong những ngôn ngữ
                lập trình HOT có cơ hội làm việc nhiều, lương cao và có tương
                lai phát triển tốt. Vì thế, Java phù hợp với đa dạng đối tượng
                người học, từ trẻ em khoảng từ 13 tuổi trở lên cho đến những
                người tay ngang sang học lập trình cũng có thể chọn Java để bắt
                đầu
              </p>
              <h4>Ruby</h4>
              <p>
                Ruby là một ngôn ngữ lập trình linh hoạt, mạnh mẽ, sử dụng trong
                phát triển web/Internet, để xử lý văn bản, tạo các trò chơi. Đây
                là ngôn ngữ mã nguồn mở, chú trọng vào sự đơn giản và hiệu suất
                cao. RuBy có một cú pháp rõ ràng, gần gũi và dễ viết, vì thế trẻ
                em rất dễ học, để phát triển tư duy và nâng cao khả năng sáng
                tạo.
              </p>
              <p>
                Trên đây là top 10 ngôn ngữ lập trình cho trẻ em tốt nhất hiện
                nay các bậc phụ huynh có thể tham khảo để tìm ra loại ngôn ngữ
                lập trình hữu ích và phù hợp nhất với con mình. Sở thích mỗi trẻ
                là khác nhau, vì thế nếu có thể các bậc phụ huynh hãy ngồi lại
                cùng bé để chơi và thăm hỏi ý kiến của con, để giúp con lựa được
                đúng loại ngôn ngữ mà chúng thích cũng như tạo tiền đề tốt, tạo
                cơ hội giúp con thành công trong tương lai.
              </p>
            </Col>
            <Col sm={4} style={{ backgroundColor: "#ededed" }}>
              <div style={{ position: "sticky", top: "0" }}>
                <h4
                  style={{
                    color: "white",
                    backgroundColor: "#afafaf",
                    padding: "3px",
                    textAlign: "center",
                  }}
                >
                  Đừng bỏ lỡ
                </h4>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </Fragment>
  );
}

export default HomePage;
