import React, { Component } from "react";
import { Container, Row, Col, Image } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "./style.css";
class Body extends Component {
  render() {
    return (
      <div style={{ marginTop: 10 }}>
        <Container>
          <Row>
            <Col sm={8} style={{ backgroundColor: "#ededed" }}>
              <h1>Người Trẻ Nên Học Lập Trình Từ Năm Mấy Tuổi?</h1>
              <h3>Tại sao nên học lập trình sớm?</h3>
              <br />
              <p>
                Học lập trình sớm mang lại nhiều lợi ích cho trẻ. Theo nghiên
                cứu, học lập trình không những giúp trẻ phát triển trí thông
                minh và tư duy mà còn giúp trẻ có nghề nghiệp ổn định với mức
                thu nhập tốt sau này.
              </p>
              <ul>
                <li>
                  <b>Hiểu rõ hơn về thế giới xung quanh</b>
                  <p>
                    Hầu hết chúng ta không biết về xuất phát điểm để tạo ra một
                    chiếc điện thoại thông minh, mạng xã hội một trò chơi điện
                    tử. Kiến thức lập trình cơ bản có thể thay đổi cách chúng ta
                    tương tác với các công nghệ mà chúng ta sử dụng hàng ngày.
                    Những kiến thức này cho phép các bạn trẻ hình thành nên thói
                    quen tư duy logic từ rất sớm, và giúp ích nhiều cho các tình
                    huống trong cuộc sống sau này.
                  </p>
                </li>
                <li>
                  <b>Nhu cầu về nghề lập trình trong tương lai</b>
                  <p>
                    Theo bls.gov, 67% trong số tất cả các công việc liên quan
                    đến STEM (Khoa học, Công nghệ, Kỹ thuật và Toán học) là về
                    máy tính. Tuy nhiên, theo khảo sát của nces.ed.gov chỉ có
                    10% sinh viên tốt nghiệp STEM theo ngành Khoa học Máy tính.
                    Điều này dẫn đến sự thiếu hụt trầm trọng về nhân lực có kỹ
                    năng và kiến thức trong ngành này:
                  </p>
                </li>
              </ul>
              <Image
                src="https://codelearn.io/Media/Default/Users/3ron/hoa_codewar/Picture1.jpg"
                fluid
              />
              <p>
                Học code sẽ tăng tỷ lệ cược của các bạn trẻ trong việc lựa chọn
                nghề nghiệp sau này, đặc biệt là trong một thế giới nơi các công
                việc liên quan đến máy tính đang phát triển ở mức cao gấp đôi.
                Lập trình đã nhanh chóng trở thành một kỹ năng quan trọng và
                Code.org cũng chỉ ra rằng các chuyên ngành khoa học máy tính có
                thể kiếm được hơn 40% so với sinh viên học các ngành khác.
              </p>
              <Image
                src="https://codelearn.io/Media/Default/Users/3ron/hoa_codewar/Background%20copy-1.jpg"
                fluid
              />
              <ul>
                <li>
                  <b>Lợi thế cạnh tranh khi nộp đơn du học</b>
                  <p>
                    Như đã nói ở trên, kỹ năng lập trình đang trở nên hot hơn
                    bao giờ hết. Vì vậy khi các bạn trẻ sở hữu kỹ năng này, họ
                    ngay lập tức trở nên nổi trội hơn trong mắt các trường đại
                    học tiềm năng khi so sánh với những người có cùng GPA hay
                    IELTS khác.
                  </p>
                </li>
                <li>
                  <b>Code là một niềm vui</b>
                  <p>
                    Mặc dù lập trình dựa trên logic, nhưng nó cũng là một hoạt
                    động cực kỳ sáng tạo. Nếu các bạn trẻ biết cách viết mã, họ
                    có thể phát triển các ứng dụng, trò chơi video, trang web và
                    hơn thế nữa. Với những người lập trình viên, lập trình có
                    thể gây stress khi gặp phải những bug không thể tìm ra cách
                    giải pháp. Nhưng đừng để điều đó đánh lừa bạn, với những bạn
                    trẻ, lập trình có thể coi là niềm vui khi thấy kết quả tạo
                    nên từ những dòng code của mình.
                  </p>
                </li>
              </ul>
              <Image
                src="https://codelearn.io/Media/Default/Users/3ron/hoa_codewar/children-593313_1920_header_code_it_studio.jpg"
                fluid
              />
              <ul>
                <li>
                  <b>Code giúp tăng sự sáng tạo</b>
                  <p>
                    Khi bạn học một ngôn ngữ, bạn sử dụng nó để thể hiện bản
                    thân. Điều tương tự cũng xảy ra với việc học lập trình. Thay
                    vì chỉ đơn giản là chơi một trò chơi hoặc sử dụng một ứng
                    dụng, các bạn trẻ có thể sáng tạo ra trò chơi hoặc các trang
                    web của riêng mình. Với những kiến thức mới mà bạn học được,
                    nó sẽ thôi thúc bạn làm ra một sản phẩm nào đó mang tính ứng
                    dụng ít nhất là với riêng bạn. Từ đó, khả năng sáng tạo của
                    bạn sẽ được phát huy hơn bao giờ hết!
                  </p>
                </li>
                <li>
                  <b>Cải thiện kỹ năng giải quyết vấn đề</b>
                  <p>
                    Khi các bạn nhỏ viết code, chúng sẽ gặp các vấn đề phức tạp
                    và được học cách chia chúng thành các phần nhỏ hơn để giải
                    quyết. Theo Dan Crow, CTO của SongKick giải thích: "Tư duy
                    lập trình dạy cho bạn cách giải quyết các vấn đề lớn bằng
                    cách chia nhỏ chúng thành một chuỗi các vấn đề nhỏ hơn, dễ
                    quản lý hơn." Tư duy logic này là một công cụ mạnh mẽ trong
                    trường học, công việc và cuộc sống.
                  </p>
                </li>
              </ul>
              <h3>Học lập trình từ mấy tuổi là hợp lý?</h3>
              <p>
                Hiện nay, có khá nhiều bậc phụ huynh quan tâm đến việc cho con
                mình học code. Theo khảo sát gần đây của{" "}
                <a href="https://csedu.gallup.com/home.aspx">
                  csedu.gallup.com
                </a>
                , 91% các vị phụ huynh muốn con em mình được học lập trình, và
                93% bố mẹ cảm thấy việc học lập trình là cần thiết trong mỗi
                trường học. Một khảo sát khác cũng chỉ ra tỉ lệ học sinh trung
                học muốn học code là rất cao, chiếm 54% những môn học được các
                bạn nhỏ yêu thích.
              </p>
              <Image
                src="https://codelearn.io/Media/Default/Users/3ron/hoa_codewar/Picturcve1-1.png"
                fluid
              />
              <p>
                Với nhu cầu bức thiết trong việc học lập trình từ sớm như vậy,
                <b>
                  {" "}
                  đâu là độ tuổi thích hợp để các bạn trẻ bắt đầu học lập trình?
                </b>
              </p>
              <p>
                Các CTO của trang web dạy lập trình cho trẻ{" "}
                <a href="https://www.tynker.com/blog/articles/ideas-and-tips/coding-at-home/at-what-age-should-kids-start-learning-programming/">
                  tynker
                </a>{" "}
                đã có một cuộc thăm dò với nhóm Hiệp hội Công nghệ Giáo dục Quốc
                tế (ISTE) trên LinkedIn về vấn đề nên giới thiệu các kỹ năng lập
                trình cho trẻ ở độ tuổi nào?
              </p>
              <p>
                <a href="https://www.tynker.com/blog/articles/ideas-and-tips/coding-at-home/at-what-age-should-kids-start-learning-programming/">
                  Đây
                </a>{" "}
                là kết quả thăm dò ý kiến (trên tổng số 168 người để lại ý
                kiến):
              </p>
              <Image
                src="https://codelearn.io/Media/Default/Users/3ron/hoa_codewar/Picturae1.png"
                fluid
              />
              <p>
                Nhìn chung, không có một quy định nghiêm khắc nào về độ tuổi bắt
                đầu học lập trình. Tuy nhiên, gần 75% số người được hỏi đều cho
                rằng học sinh nên học viết code từ tiểu học. Điều này có ý nghĩa
                <b>
                  {" "}
                  khi học một ngôn ngữ lập trình rất giống với việc học một ngôn
                  ngữ thực tế:
                </b>{" "}
                càng sớm, càng tốt.
              </p>
              <p>
                Theo nghiên cứu khoa học, trẻ em từ 7 tuổi trở lên có thể bắt
                đầu viết code và học lập trình cơ bản. Ở độ tuổi này, trí não
                của các em đã phát triển đủ để tiếp thu về các khái niệm lập
                trình cơ bản, bên cạnh đó, học lập trình ở độ tuổi này sẽ phát
                huy khả năng tư duy logic của trẻ sau này. Tuy nhiên, nói vậy
                không đồng nghĩa với việc học lập trình là muộn đối với các bạn
                học sinh cấp 2, cấp 3. Ở mỗi độ tuổi khác nhau, các em có thể
                lựa chọn cho mình một cách học khác nhau để có thể phát huy tối
                đa khả năng của mình.
              </p>
              <h2>KẾT</h2>
              <p>
                Dạy học lập trình cho trẻ từ sớm là một điều cần thiết để trẻ
                phát triển tư duy tốt hơn cũng như phát triển các kỹ năng mềm
                khác. Cho trẻ học lập trình chính là vẽ nên tương lai rộng mở
                hơn cho các bạn trẻ. Nhưng trên hết, hãy tôn trọng sở thích và
                lựa chọn của các con, để trẻ em không cảm thấy áp lực khi tham
                gia bất kì môn học nào.
              </p>
            </Col>
            <Col sm={4} style={{ backgroundColor: "#ededed" }}>
              <div style={{ position: "sticky", top: "0" }}>
                <h4
                  style={{
                    color: "white",
                    backgroundColor: "#afafaf",
                    padding: "3px",
                    textAlign: "center",
                  }}
                >
                  Đừng bỏ lỡ
                </h4>
              </div>
            </Col>
          </Row>
          {/* <Exercise /> */}
        </Container>
      </div>
    );
  }
}

export default Body;
