import { Button, Grid, Typography, withStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { compose } from "redux";
import style from "./style";
import CheckIcon from "@material-ui/icons/Check";
import Countdown, { zeroPad } from "react-countdown";
import ReplayIcon from "@material-ui/icons/Replay";
import { formValueSelector } from "redux-form";
import { connect } from "react-redux";
const renderer = ({ hours, minutes, seconds, completed }) => {
  if (completed) {
    return <div>ABC</div>;
  } else {
    return (
      <div style={{ fontSize: 29 }}>
        {zeroPad(hours)} : {zeroPad(minutes)} : {zeroPad(seconds)}
      </div>
    );
  }
};
var lengthData = 0;
function CourseList(props) {
  const { length, classes, order, data, time, pause } = props;
  const [initialTime, setInitialTime] = useState(Date.now() + 10000);
  function onChangeOrder(item) {
    props.onChangeOrder(item);
  }
  function onRework() {
    props.onRework();
    setInitialTime(Date.now() + time);
  }
  function complelte() {
    props.onComplete();
  }
  function renderItem() {
    lengthData = length;
    var arr = [];
    if (length > 0) {
      for (var i = 1; i <= length; i++) {
        arr.push(i);
      }
      return arr.map((item, index) => {
        return (
          <Grid
            key={index}
            item
            md={2}
            className={classes.rootItem}
            style={{
              color: item === order ? "red" : "",
            }}
          >
            <div
              className={classes.item}
              style={{
                backgroundColor:
                  data[item - 1] && data[item - 1].value ? "silver" : "",
              }}
              onClick={() => {
                onChangeOrder(item);
              }}
            >
              {item}
            </div>
          </Grid>
        );
      });
    }
  }
  useEffect(() => {
    setInitialTime(Date.now() + time);
  }, [time]);
  return (
    <div style={{ paddingRight: 30 }}>
      <Grid container className={classes.header}>
        <Grid md={3}>
          <Button style={{ display: "block", marginLeft: 20 }}>
            <CheckIcon />
            <Typography>Kết quả</Typography>
          </Button>
        </Grid>
        <Grid
          md={6}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Countdown
            date={initialTime}
            onComplete={complelte}
            renderer={renderer}
            controlled={pause}
          />
        </Grid>
        <Grid md={3}>
          <Button
            style={{ display: "block", marginLeft: 10 }}
            onClick={onRework}
          >
            <ReplayIcon />
            <Typography>Làm lại</Typography>
          </Button>
        </Grid>
      </Grid>
      <Grid container style={{ border: "1px solid black", borderTop: 0 }}>
        {renderItem()}
      </Grid>
    </div>
  );
}
const selector = formValueSelector("TEST_FORM");
const mapStateToProps = (state) => {
  var data = [];
  for (var i = 1; i <= lengthData; i++) {
    data.push({
      numberorder: i,
      value: selector(state, `testQuestion${i}`),
    });
  }
  return {
    data,
  };
};
const withConnect = connect(mapStateToProps, null);
export default compose(withStyles(style), withConnect)(CourseList);
